.sticky {
  display: block;
}

#page.site {
  position: relative;
  min-height: 100%;
}

#masthead {
  background: #17252e;
  color: #fff;
  line-height: 1.3;
  font-size: 14px;

  .row-w-logo {
    padding: 15px;

    .site-branding {
      text-align: center;
    }

    .social-icons {
      font-size: 30px;

      i {
        @include prefix(transition, all .2s ease-in-out, webkit moz o);
        padding: 2%;

        &:hover {
          color: #3ebea3;
        }
      }
    }

    .contact-info {
      text-align: center;
      font-size: 16px;
      font-weight: 200;

      .contact-message {
        font-weight: 700;
        padding-bottom: 10px;
      }

      i {
        font-size: 22px;
        padding: 0 10px;
        vertical-align: middle;
      }
    }
  }
}

section {
  overflow: hidden;
}

#above-fold {
  height: 600px;

  #header-media-wrap {
    position: relative;
    padding: 0;

    .header-message {
      position: absolute;
      top: 35%;
      right: 0;
      @include prefix( transform, translateY(-50%), webkit moz o );
      z-index: 100;

      p {
        padding: 15px;
        text-align: left;
        background: rgba(#fff, 0.7);
      }

      .wonka-btn {
        position: absolute;
        left: 10%;
        border-radius: 0;
        padding: 8px 25px;
        font-size: 20px;
        font-family: Open Sans;
        font-weight: 800;
        border: none;
        color: $header__background;
        background: $product__sectionlite;
      }
    }

    img {
      width: 100%;
    }
  }
}

#product-section {
  position: relative;
  background: linear-gradient(-213deg, $product__sectionlite 100%, $product__sectiondark 100%);

  &:before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    display: block;
    height: 6px;
    background: $product__topborder;
    z-index: 100;
  }

  .container-fluid {
    height: 100%;

    .row {
      height: 100%;
    }
  }

  .product-icon {
    display: flex;
    text-align: center;
    align-self: center;
    align-items: flex-end;
    font-size: 40px;

    &.product-icon-middle i {
      color: $header__background;
      font-size: 40px;
      background: #fff;
    }

    i {
      position: relative;
      display: flex;
      align-self: center;
      align-items: center;
      justify-content: center;
      margin-bottom: 15px;
      color: #fff;
      height: 100px;
      width: 100px;
      padding: 15px;
      text-align: center;
      font-size: 45px;
      line-height: 1.5;
      border-radius: 50%;
      background: $header__background;
    }
  }

  .product-copy {
    padding: 15px 0;
    align-self: center;
  }
}

#logos-section {
  position: relative;
  background: $header__background;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    display: block;
    height: 6px;
    background: #fff;
    z-index: 100;
  }

  .row {
    padding: 10px 0;
  }
}

#services-section {
  position: relative;
  height: 400px;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    display: block;
    height: 6px;
    background: linear-gradient(-213deg, $product__sectionlite 100%, $product__sectiondark 100%);
    z-index: 100;
  }

  .services-title-row {
    position: relative;
    margin-top: 25px;
    z-index: 50;

    .services-title {
      margin: auto;
      border: 2px solid $header__background;

      h2 {
        align-self: center;
        color: $header__background;
        font-weight: 700;
        margin: 0;
        padding: 5px 20px;
      }
    }
  }

  .services-items {
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;

    > div:nth-child(2n+1) {
      background: $product__topborder;
    }

    > div:nth-child(2n) {
      background: $nav__background;
    }

    > div > .service-item {
      @include prefix(transform, translateX(-50%), webkit moz o);
      position: relative;
      top: 95px;
      left: 50%;
      padding: 15px;
      text-align: center;

      .service-icon {
        display: flex;
        justify-content: center;

        i {
          display: flex;
          margin-bottom: 15px;
          color: #fff;
          height: 150px;
          width: 150px;
          padding: 25px;
          align-items: center;
          justify-content: center;
          font-size: 55px;
          line-height: 1;
          border-radius: 50%;
          background: $header__background;
        }
      }


      h3 {
        font-weight: 700;
      }
    }
  }
}

#team-section {
  position: relative;
  height: 375px;
  background: $product__topborder;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    display: block;
    height: 6px;
    background: #fff;
    z-index: 100;
  }

  .bg-row {
    position: absolute;
    height: 100%;
    top: 0;
    left: -325px;
    right: -200px;

    .col {
      @include prefix(transform, matrix(1, 0, -2.2, 1, 0, 0), webkit moz o);
    }

    .col:first-child,
    .col:nth-child(2n+1) {
      border-left: 16px solid #fff;
      border-right: 16px solid #fff;
      background: $header__background;
    }

    .col:nth-child(2),
    .col:nth-child(6) {
      border-left: 16px solid transparent;
      border-right: 16px solid transparent;
      background: $product__sectionlite;
    }
  }

  .team-title-row {
    position: relative;
    margin-top: 25px;

    .team-title {
      margin: auto;
      border: 2px solid #fff;
      background: $product__sectionlite;

      h2 {
        align-self: center;
        color: $header__background;
        text-transform: uppercase;
        margin: 0;
        padding: 5px 20px;
      }
    }
  }
}

#work-section {
  position: relative;
  height: 575px;
  background: $header__background;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    display: block;
    height: 6px;
    background: #fff;
    z-index: 100;
  }

  .bg-row {
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;

    .bg-col {
      width: 145px;
      border-left: 1px solid rgba(#fff, .05);
      border-right: 1px solid rgba(#fff, .05);
      background: rgba(#fff, .05);
    }

    .bg-col:nth-child(2),
    .bg-col:nth-child(4),
    .bg-col:nth-child(5) {
      width: 35px;
    }

    .bg-col:first-child {
      width: 90px;
    }

    .bg-col:last-child {
      margin-right: 5%;
    }
  }

  .work-title-row {
    margin-top: 25px;

    .work-title {
      margin: auto;
      border: 2px solid #fff;

      h2 {
        color: #fff;
        text-transform: uppercase;
        font-weight: 700;
        margin: 0;
        padding: 5px 20px;
      }
    }
  }
}

#responsive-section {
  position: relative;
  height: 375px;
  background: $product__sectionlite;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    display: block;
    height: 6px;
    background: #fff;
    z-index: 100;
  }

  .bg-row {
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;

    .bg-col {
      width: 100px;
      border-left: 1px solid rgba(#fff, 0.3 );
      border-right: 1px solid rgba(#fff, 0.3 );
      background: rgba(#fff, 0.3 );
    }
    .bg-col:nth-child(2),
    .bg-col:nth-child(9),
    .bg-col:nth-child(10) {
      width: 55px;
    }

    .bg-col:nth-child(2) {
      margin-left: 2%; 
    }

    .bg-col:nth-child(3) {
      margin-left: 3%; 
    }

    .bg-col:nth-child(4) {
      margin-left: 6%; 
    }

    .bg-col:nth-child(5) {
      margin-left: -0.3%;
    }

    .bg-col:nth-child(6) {
      margin-left: 2%;
    }

    .bg-col:nth-child(7) {
      margin-left: 2%;
    }

    .bg-col:nth-child(8) {
      margin-left: 6%;
    }

    .bg-col:nth-child(9) {
      margin-left: 3%;
    }

    .bg-col:nth-child(4),
    .bg-col:nth-child(5) {
      width: 45px;
    }

    .bg-col:nth-child(3) {
      width: 15px;
    }

    .bg-col:first-child,
    .bg-col:nth-child(7),
    .bg-col:nth-child(8) {
      width: 20px;
    }

    .bg-col:nth-child(10) {
      width: 40px;
    }

    .bg-col:first-child {
      margin-left: 3%;
    }

    .bg-col:last-child {
      margin-right: 5%;
    }
  }
}

#vision-section {
  position: relative;
  height: 350px;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    display: block;
    height: 6px;
    background: $header__background;
    z-index: 100;
  }

  .container-fluid {
    position: relative;
    height: 100%;

    > .row {
      height: 100%;

      .vision-side {
        border-right: 2px dashed #fff;
        background: $nav__background;

        .vision-title-row {
          margin-top: 25px;

          .vision-title {
            padding: 10px 25px;
            background: $product__topborder;

            h2 {
              margin: 0;
              color: #fff;
              text-transform: uppercase;
              font-weight: 700;
            }
          }
        }

        .vision-copy-row {
          margin: auto;

          .vision-copy {
            background: rgba(#fff, .3);
            margin: 25px auto;
            padding: 15px;
            width: 50%;
            color:#000;
            font-weight: 800;

            p {
              margin: 0;
            }
          }
        }
      }

      .mission-side {
        border-left: 2px dashed #fff;
        background: $product__topborder;

        .mission-title-row {
          margin-top: 25px;

          .mission-title {
            padding: 10px 25px;
            background: $nav__background;

            h2 {
              margin: 0;
              color: #fff;
              text-transform: uppercase;
              font-weight: 700;
            }
          }
        }

        .mission-copy-row {
          clear: both;
          margin: auto;

          .mission-copy {
            background: rgba(#fff, .3);
            margin: 25px auto;
            padding: 15px;
            width: 50%;
            color:#000;
            font-weight: 800;

            p {
              margin: 0;
            }
          }
        }
      }
    }
  }
}

#testimonials-section {
  position: relative;
  height: 450px;
  background: $product__sectionlite;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    display: block;
    height: 6px;
    background: $header__background;
    z-index: 100;
  }

  .bg-row {
    @include prefix(transform, matrix(1, -.28, -500, 1, 0, 0), webkit moz o);
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;

    .bg-col {
      width: 275px;
      border-left: 1px solid rgba(#fff, .3);
      border-right: 1px solid rgba(#fff, .3);
      background: rgba(#fff, .3);
    }

    .bg-col:nth-child(1) {
      margin-left: 8%;
      width: 60px;
    }

    .bg-col:nth-child(3) {
      margin-left: 5%;
      width: 155px;
    }
  }

  .testimonials-title-row {
    margin-top: 25px;

    .testimonials-title {
      margin: auto;
      border: 2px solid #fff;

      h2 {
        align-self: center;
        color: #fff;
        text-transform: uppercase;
        font-weight: 700;
        margin: 0;
        padding: 5px 20px;
      }
    }
  }
}

#recent-post-section {
  position: relative;
  height: 450px;
  background: $product__topborder;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    display: block;
    height: 6px;
    background: #e0e0e0;
    z-index: 100;
  }

  .recent-post-bg {
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;

    .first-col {
      height: 100%;
    }

    .center-col {
      height: 100%;
      width: 27%;
      background:#e0e0e0;
      @include prefix(transform, matrix(1, 0, -.5, 1, 0, 0), webkit moz o);
    }

    .last-col {
      height: 100%;
    }
  }

  .section-title {
    background: #e0e0e0;
    padding: 10px 20px;

    h2 {
      white-space: nowrap;
      color: #3cc2a5;
      font-size: 34px;
      font-weight: 800;
      text-transform: uppercase;
    }
  }

  .recent-post-items {

    .blog-post {
      text-align: center;
      padding: 30px;

      h3 {
        color: #000;
        font-weight: 800;
      }

      .wonka-btn {
        color: $header__background;
        padding: 10px 25px;
        border-radius: 4px;
        text-transform: uppercase;
        font-weight: 800;
        background: transparent;
        border: 1px solid $header__background;

        &:hover, &:active, &:focus {
          color: #fff;
          text-decoration: none;
          background: $header__background;
        }
      }
    }
  }
}

#newsletter-section {
  position: relative;
  background: #e0e0e0;

  h2 {
    white-space: nowrap;
    font-size: 34px;
    font-weight: 800;
    color: $header__background;
  }

  .newsletter-form {
    padding: 10px 0;
    align-self: center;

    .wpforms-container {
      margin: 0;

      .wpforms-field-container {
        width: calc(100% - 200px);
        padding: 0;

        .wpforms-field-required {
          height: 48px;
        }
      }

      .wpforms-submit-container {
        margin-top: 6px;
        width: 185px;
      }

      .wonka-btn {
        background: $header__background;
        color: #fff;
        font-weight: 600;
        border-radius: 4px;
        text-transform: uppercase;
        white-space: nowrap;
      }
    }
  }

  input {
    display: inline-block;
  }
}

.hentry {
  margin: 0 0 1.5em;
}

.updated:not(.published) {
  display: none;
}

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5em 0 0;
}

.page-links {
  clear: both;
  margin: 0 0 1.5em;
}

footer {
  position: absolute;
  top: 100%;
  width: 100%;
  color: #fff;
  background: $header__background;

  h3 {
    color: $product__topborder;
    font-weight: 700;
  }

  .site-info {
    position: relative;
    display: block;
    padding: 25px;
    text-align: center;
    font-family: Roboto;
    font-weight: 200;

    a {
      color: $product__sectionlite;
      font-weight: normal;
      @include prefix( transition, all 0.2s ease-in-out, webkit moz o);

      &:hover {
        text-decoration: none;
        font-weight: normal;
        color: #3ebea3;
      }
    }
  }

  .social-icons {
    font-size: 30px;

    i {
      padding: 2%;
      @include prefix( transition, all 0.2s ease-in-out, webkit moz o);

      &:hover {
        color: $product__sectionlite;
      }
    }
  }
}

#to-top-btn {
  @include prefix(transform, rotate(90deg), webkit moz);
  @include prefix(box-shadow, 2px 0 6px 2px rgba(#000,.3), webkit moz o);
  position: fixed;
  right:35px;
  bottom:35px;
  height:50px;
  width:50px;
  font-size:75px;
  line-height:0.45;
  border-radius:50%;
  background:#fbc926;
  z-index:999;

  &:hover {
    text-decoration: none;
  }
}
